@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
:root{--animate-duration: 800ms}*{font-family:'Nunito', sans-serif !important;font-size:1rem !important}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.navbar{box-shadow:0px 5px 20px rgba(0,0,0,0.2)}.navbar .logo{width:16rem}.navbar a{color:#ffcd3c;transition:all 0.3s}.navbar a:hover{color:#cca52e}.content-center{justify-content:center !important;align-items:center}.button{border-color:transparent !important;font-weight:bold;color:#fff !important;transition:all 0.3s;width:100%}.button:focus,.button:active{box-shadow:0 0 0 0.125em rgba(217,32,39,0.25) !important}.button.btn-primary{background-color:#ffcd3c}.button.btn-primary:hover{background-color:#cca52e}.button.btn-secondary{background-color:#35d0ba}.button.btn-secondary:hover{background-color:#279e8c}.button.btn-delete{background-color:#d92027;width:3rem;font-family:'Font Awesome\ 5 Free' !important}.button.btn-delete:hover{background-color:#ad191e}.button.btn-add,.button.btn-rnd{flex:0.48 1}.Home{flex-flow:column nowrap !important}.inText{flex:0.98 1;border-color:#dbdbdb !important;transition:all 0.3s}.inText:focus,.inText:active{box-shadow:0 0 8px rgba(255,205,60,0.5)}.in-container{justify-content:space-between;align-items:center}@media screen and (max-width: 768px){.navbar{flex-flow:column nowrap}}

